export var LocalStorage = {
    touch: function (key, defaultValue) {
        if (defaultValue === void 0) { defaultValue = null; }
        try {
            var rawValue = window.localStorage.getItem(key);
            if (rawValue === null || rawValue === undefined) {
                LocalStorage.set(key, defaultValue);
                return defaultValue;
            }
            return JSON.parse(rawValue);
        }
        catch (err) {
            console.error("Error getting key ".concat(key, " from localStorage"), err);
            return null;
        }
    },
    set: function (key, value) {
        try {
            var serializedValue = JSON.stringify(value);
            window.localStorage.setItem(key, serializedValue);
        }
        catch (err) {
            console.error("Error setting key ".concat(key, " to localStorage"), err);
        }
    },
    remove: function (key) {
        try {
            window.localStorage.removeItem(key);
        }
        catch (err) {
            console.error("Error removing key ".concat(key, " from localStorage"), err);
        }
    },
    clear: function () {
        try {
            window.localStorage.clear();
        }
        catch (err) {
            console.error("Error clearing localStorage", err);
        }
    },
};
